/**
* Vue Router.
* -------------------------------------------------------------
* Routes defined in this file may have the following meta attributes:
*   title:       Route L10n name key. Set to false to hide title and breadcrumbs.
*   rule:        Required ACL role for this route.
*   breadcrumbs: List of breadcrumbs to display for this route.
*      title:    Breadcrumb L10n name key.
*      url:      Breadcrumb URL.
*      active:   Active breadcrumb indicator.
*   tools:       List of tools to display on the right of breadcrumbs.
*      title:    Tool L10n name key.
*      icon:     Tool icon name.
*      toolId:   Tool ID to be used in emitted event.
*/

import Vue from 'vue'
import Router from 'vue-router'
import store from '@/store/store'
import base from '@/router/base'
import dashboard from '@/router/dashboard'
import setup from '@/router/setup'
import bank from '@/router/bank'
import agreement from '@/router/agreement'
import party from '@/router/party'
import profile from '@/router/profile'
import ft from '@/router/ft'
import workflow from '@/router/workflow'
import report from '@/router/report'
import SecurityService from '@/services/nucleus/SecurityService'

Vue.use(Router)

const router = new Router({
  mode: 'hash',
  base: process.env.BASE_URL,
  scrollBehavior () {
    return { x: 0, y: 0 }
  },
  routes: [base, {
    path: '/secure',
    component: () => import('@/layouts/main/Main'),
    redirect: { name: 'Dashboard' },
    children: [
      dashboard,
      setup,
      bank,
      agreement,
      party,
      profile,
      ft,
      workflow,
      report
    ]
  }, {
    path: '*',
    redirect: '/error/404'
  }]
})

// redirect to login page if session not authenticated
router.beforeEach((to, from, next) => {
  if (to.meta.rule === 'isPublic' || store.getters.isSessionAuthenticated()) {
    return next()
  } else {
    router.push({ path: '/login' })
  }
})

// remove initial loading after each route
router.afterEach((to, from) => {
  const appLoading = document.getElementById('loading-bg')
  if (appLoading) {
    appLoading.style.display = 'none'
  }

  if (store.getters.isSessionAuthenticated()) {
    SecurityService.poll({}).then(function (response) {
      if (response.data.sessionId) {
        store.dispatch('setSession', {
          sessionId: response.data.sessionId,
          sessionValidity: response.data.sessionValidity,
          sessionToken: response.data.sessionToken
        })
        store.dispatch('setWorkflowTasks', {
          workflowTasks: response.data.workflowTasks
        })
      }
    })
  }
})

export default router
