import Vue from 'vue'
import { AclInstaller, AclCreate, AclRule } from 'vue-acl'
import router from '@/router/router'
import store from '@/store/store'

Vue.use(AclInstaller)

export default new AclCreate({
  initial: 'public',
  notfound: '/error/403',
  router,
  acceptLocalRules: false,
  globalRules: {
    isAccountAll: new AclRule('BNQ_ADM_ACCT_ALL').generate(),
    isAccountView: new AclRule('BNQ_ADM_ACCT_ALL').or('BNQ_ADM_ACCT_VIEW').generate(),
    isBankAll: new AclRule('BNQ_ADM_BANK_ALL').generate(),
    isBankView: new AclRule('BNQ_ADM_BANK_ALL').or('BNQ_ADM_BANK_VIEW').generate(),
    isBeneficiaryAll: new AclRule('BNQ_ADM_BEN_ALL').generate(),
    isBeneficiaryView: new AclRule('BNQ_ADM_BEN_ALL').or('BNQ_ADM_BEN_VIEW').generate(),
    isCardAll: new AclRule('BNQ_ADM_CARD_ALL').generate(),
    isCardView: new AclRule('BNQ_ADM_CARD_ALL').or('BNQ_ADM_CARD_VIEW').generate(),
    isChequeAll: new AclRule('BNQ_ADM_CHQ_ALL').generate(),
    isChequeView: new AclRule('BNQ_ADM_CHQ_ALL').or('BNQ_ADM_CHQ_VIEW').generate(),
    isFundsTransferAll: new AclRule('BNQ_ADM_FT_ALL').generate(),
    isFundsTransferView: new AclRule('BNQ_ADM_FT_ALL').or('BNQ_ADM_FT_VIEW').generate(),
    isLoanAll: new AclRule('BNQ_ADM_LOAN_ALL').generate(),
    isLoanView: new AclRule('BNQ_ADM_LOAN_ALL').or('BNQ_ADM_LOAN_VIEW').generate(),
    isNucleusAll: new AclRule('BNQ_ADM_NCLS_ALL').generate(),
    isNucleusView: new AclRule('BNQ_ADM_NCLS_ALL').or('BNQ_ADM_NCLS_VIEW').or('BNQ_ADM_NCLS_RESCUE').generate(),
    isNucleusRescue: new AclRule('BNQ_ADM_NCLS_ALL').or('BNQ_ADM_NCLS_RESCUE').generate(),
    isSweepAll: new AclRule('BNQ_ADM_SWEEP_ALL').generate(),
    isSweepView: new AclRule('BNQ_ADM_SWEEP_ALL').or('BNQ_ADM_SWEEP_VIEW').generate(),
    isTradeFinanceAll: new AclRule('BNQ_ADM_TF_ALL').generate(),
    isTradeFinanceView: new AclRule('BNQ_ADM_TF_ALL').or('BNQ_ADM_TF_VIEW').generate(),
    isReportAll: new AclRule('BNQ_ADM_RPT_ALL').generate(),
    isReportView: new AclRule('BNQ_ADM_RPT_ALL').or('BNQ_ADM_RPT_VIEW').or('BNQ_ADM_RPT_RUN').generate(),
    isReportRun: new AclRule('BNQ_ADM_RPT_ALL').or('BNQ_ADM_RPT_RUN').generate(),
    isLimitAll: new AclRule('BNQ_ADM_LMT_ALL').generate(),
    isLimitView: new AclRule('BNQ_ADM_LMT_ALL').or('BNQ_ADM_LMT_VIEW').generate(),
    isCustomerView: new AclRule('BNQ_ADM_BANK_ALL').or('BNQ_ADM_BANK_VIEW')
      .or('BNQ_ADM_NCLS_ALL').or('BNQ_ADM_NCLS_VIEW').or('BNQ_ADM_NCLS_RESCUE')
      .or('BNQ_ADM_SWEEP_ALL').or('BNQ_ADM_SWEEP_VIEW')
      .generate(),
    isPartiallyAuthenticated: new AclRule('PARTIAL_AUTH_USERS'),
    isFullyAuthenticated: new AclRule('BNQ_ADM_ACCT_VIEW').or('BNQ_ADM_ACCT_ALL')
      .or('BNQ_ADM_BANK_VIEW').or('BNQ_ADM_BANK_ALL')
      .or('BNQ_ADM_BEN_VIEW').or('BNQ_ADM_BEN_ALL')
      .or('BNQ_ADM_CARD_VIEW').or('BNQ_ADM_CARD_ALL')
      .or('BNQ_ADM_CHQ_VIEW').or('BNQ_ADM_CHQ_ALL')
      .or('BNQ_ADM_LOAN_VIEW').or('BNQ_ADM_LOAN_ALL')
      .or('BNQ_ADM_NCLS_VIEW').or('BNQ_ADM_NCLS_ALL').or('BNQ_ADM_NCLS_RESCUE')
      .or('BNQ_ADM_SWEEP_VIEW').or('BNQ_ADM_SWEEP_ALL')
      .or('BNQ_ADM_TF_ALL').or('BNQ_ADM_TF_VIEW')
      .or('BNQ_ADM_FT_ALL').or('BNQ_ADM_FT_VIEW')
      .or('BNQ_ADM_RPT_ALL').or('BNQ_ADM_RPT_VIEW').or('BNQ_ADM_RPT_RUN')
      .generate(),
    isPublic: new AclRule('public')
      .or('ALL_USERS')
      .generate()
  },
  middleware: async acl => {
    acl.change(store.state.user.userRoles || 'public')
  }
})
